@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-contact {
  position: relative;
  .com-introduce {
    .wrapper-content {
      padding: 0;
    }
  }
  .btn-contact {
    margin: 0 $pdMobile;
    @include btn();
  }
  input[type="checkbox"],
  input[type="radio"] {
    display: none;
    position: absolute;
    opacity: 0;
    z-index: -1;
  }
  input#checkbox {
    display: unset;
    opacity: unset;
    position: unset;
    -webkit-appearance: checkbox;
    width: unset;
    @include desktop2k {
      transform: scale(2);
    }
  }
  /* Accordion styles */
  .tabs {
    margin: $pdMobile;
  }

  .tab {
    width: 100%;
    color: white;
    overflow: hidden;
    margin-bottom: 1rem;
    &-label {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 1em;
      background: lighten(#000000, 5%);
      @include title();
      cursor: pointer;

      .remove {
        display: none;
        color: #ff3050;
      }
      .add {
        color: #ff3050;
        display: block;
      }
    }
    &-content {
      max-height: 0;
      opacity: 0;
      visibility: hidden;
      padding: 0 1em;
      color: black;
      background: white;
      transition: all 0.5s ease;
      font-size: 1rem;
    }
  }

  // :checked
  input:checked {
    + .tab-label {
      background: black;
      .add {
        display: none;
      }
      .remove {
        display: block;
      }
    }
    ~ .tab-content {
      opacity: 1;
      visibility: visible;
      max-height: 350vh;
      padding: 1em;
    }
  }
  .list-contact {
    .contact {
      margin-bottom: 1.25rem;
      .city {
        @include title();
        font-size: 1.15rem;
        margin-bottom: 0.25rem;
        span {
          margin-left: 5px;
        }
      }
      .address {
        line-height: 1.25;
      }
      > div {
        display: flex;
        align-items: center;
        margin-bottom: 0.5rem;
        @include desktop2k {
          font-size: 1.5rem;
          margin-bottom: 1rem;
        }
        svg {
          margin-right: 5px;
          @include desktop2k {
            margin-right: 15px;
            width: 50px;
            height: 50px;
          }
        }
      }
      .btn-email {
        font-weight: 600;
        cursor: pointer;
        user-select: none;
      }
    }
  }
}
@include desktop {
  .com-contact {
    margin-left: 5vw;
    .com-introduce {
      .title {
        margin-top: 13vh;
      }
      .block-content {
        left: 0;
        right: 0;
        max-width: 500px;
        margin-top: $pdDesk;
        margin: auto;
        @include desktop2k {
          max-width: 1000px;
        }
      }
      text-align: center;
    }
    .btn-contact {
      position: absolute;
      top: 46vh;
      left: 25%;
      width: 200px;
      margin: unset;
      transform: translate(-50%, 0);
      @include desktop2k {
        width: 500px;
        font-size: 1.35rem;
        padding: 1.5rem;
      }
    }
    .tabs {
      margin: $pdDesk;
      .tab {
        width: 100%;
        color: white;
        overflow: hidden;
        &-label {
          font-size: 1.5rem;
          @include desktop2k {
            font-size: 2rem;
          }
          .remove,
          .add {
            font-size: 2rem;
            // color: white;
            @include desktop2k {
              font-size: 2.5rem;
            }
          }
        }
      }
    }

    .list-contact {
      display: grid;
      grid-gap: 0 5vw;
      grid-template-columns: 1fr 1fr 1fr;
      @include desktop2k {
        grid-template-columns: 1fr 1fr 1fr 1fr;
      }
      .contact {
        .city {
          font-size: 1.5rem;
          @include desktop2k {
            font-size: 1.75rem;
          }
        }
        .btn-email {
          font-weight: 600;
        }
      }
    }
  }
}
