.dropdown {
  position: relative;
  display: block;

  &::before {
    content: url("./tick.svg");
    position: absolute;
    pointer-events: none;
    top: 50%;
    right: 8px;
    transform: translateY(-50%);
  }

  input[type="text"] {
    font-size: 1rem;
    width: 100%;
    height: 100%;
    line-height: 100%;
    outline: none;
  }

  select {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
  }

  .placeholder {
    position: absolute;
    overflow: hidden;
    text-align: left;
    pointer-events: none;
    user-select: none;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
  }
  &.active .placeholder {
    display: none;
  }
}
