@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";

.com-form-contact {
  width: 100vw;
  font-size: 1rem;
  box-sizing: border-box;
  padding: 0 2rem;
  transition: 0.25s all ease;
  z-index: 99;

  @include desktop() {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    max-width: 47vw;
    padding: 1.5rem 3rem;
    padding-bottom: 0;
    overflow: hidden;
  }
  h4 {
    text-align: center;
    margin: 1rem auto;
    @include desktop {
      margin: 1rem auto;
      max-width: 70%;
    }
    @include desktop2k() {
      font-size: 1.5rem;
      margin: 2rem auto;
    }
  }
  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type="number"] {
    -moz-appearance: textfield;
  }

  & > img {
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;
    transform: scale(0.8);
    cursor: pointer;
    @include desktop() {
      right: 1.5rem;
      top: 1.5rem;
      transform: unset;
      @include desktop2k {
        right: 2rem;
        top: 2rem;
        transform: scale(1.5);
      }
    }
  }

  input,
  select,
  textarea {
    display: block;
    width: 100%;
    border: 1px solid #6161619d;
    border-radius: 6px;
    padding: 0.5rem 0.75rem;
    -webkit-appearance: none;
    background-color: transparent;
    outline: none;
    transition: 0.4s;
    font-size: 0.85rem;
    font-family: "Lato", sans-serif;
    @include desktop2k() {
      font-size: 1.5rem;
    }
  }

  .title {
    @include title();
    font-size: 1.5rem;
    width: fit-content;
    margin: auto;
    white-space: nowrap;
    @include desktop() {
      font-size: 2.5rem;
    }
    @include desktop2k() {
      font-size: 3.5rem;
    }
  }
  .sub-tit {
    margin-bottom: 1rem;
  }
  form {
    @include desktop() {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 0 2rem;
    }
    .item {
      position: relative;
      margin-bottom: 0.5rem;
      @include desktop2k() {
        margin-bottom: 1rem;
      }
      &.mess {
        grid-column: 1/3;
      }
      &.checkbox {
        grid-column: 1/3;
        label,
        .recaptcha {
          font-size: 0.85rem;
          font-weight: 400;
          margin-left: 10px;
          margin-top: 0.75rem;
          cursor: pointer;
          &.mg-top0 {
            margin-left: 0;
            cursor: default;
          }
          a {
            font-weight: 700;
            text-decoration: underline;
            margin: 0 4px;
          }
          @include desktop2k {
            font-size: 1.25rem;
          }
        }
      }
      .dropdown {
        margin-top: 0.3rem;
        &::before {
          top: 43%;
          transform: translateY(-50%) scale(1.35);
        }
      }
      .label {
        @include title();
        text-transform: unset;
        font-size: 0.85rem;
        @include desktop2k() {
          font-size: 1.25rem;
        }
      }
      .errCheck {
        position: absolute;
        color: #ff7474;
        font-weight: 700;
        font-size: 0.85rem;
        @include desktop2k() {
          font-size: 1.25rem;
        }
      }
      .error {
        position: absolute;
        right: 0;
        top: -1.2rem;
        background-color: #ff7474;
        border-radius: 5px;
        color: white;
        padding: 0.75rem 1rem;
        z-index: 5;
        font-size: 0.9rem;

        &:after {
          content: " ";
          position: absolute;
          bottom: -15%;
          right: 10px;
          width: 0;
          height: 0;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
          border-top: 10px solid #ff7474;
        }
        & + input,
        & + textarea {
          border-color: #ff7474;
        }
      }
      > input,
      > select,
      > textarea {
        margin-top: 0.35rem;
        @include desktop2k {
          margin-top: 0.5rem;
        }
        &::placeholder {
          color: gray;
        }
      }
      > svg {
        position: absolute;
        right: 0.5rem;
        top: 50%;
      }
      @include desktop() {
        textarea {
          min-height: 100px;
        }
      }
    }
  }
  .btn-submit {
    @include btn();
    width: 100%;
    outline: none;
    border: none;
    margin: 1.5rem 0;
    transition: 0.4s background-color ease;
    &:disabled {
      background-color: darkgrey;
    }
    @include desktop() {
      margin: auto;
      margin-top: 1.5rem;
      max-width: 15vw;
      min-width: 300px;
      grid-column: 1/3;
    }
    @include desktop2k() {
      font-size: 1.25rem;
      padding: 1.5rem;
      border-radius: 50px;
    }
  }
}
